    }

    .kt {
        /* Keyword.Type */
        color: $base0a;
    }

    .ld {
        /* Literal.Date */
        color: $base0b;
    }

    .m {
        /* Literal.Number */
        color: $base09;
    }

    .s {
        /* Literal.String */
        color: $base0b;
    }

    .na {
        /* Name.Attribute */
        color: $base0d;
    }

    .nb {
        /* Name.Builtin */
        color: $base05;
    }

    .nc {
        /* Name.Class */
        color: $base0a;
    }

    .no {
        /* Name.Constant */
        color: $base08;
    }

    .nd {
        /* Name.Decorator */
        color: $base0c;
    }

    .ni {
        /* Name.Entity */
        color: $base05;
    }

    .ne {
        /* Name.Exception */
        color: $base08;
    }

    .nf {
        /* Name.Function */
        color: $base0d;
    }

    .nl {
        /* Name.Label */
        color: $base05;
    }

    .nn {
        /* Name.Namespace */
        color: $base0a;
    }

    .nx {
        /* Name.Other */
        color: $base0d;
    }

    .py {
        /* Name.Property */
        color: $base05;
    }

    .nt {
        /* Name.Tag */
        color: $base0c;
    }

    .nv {
        /* Name.Variable */
        color: $base08;
    }

    .ow {
